.card-movie {
	display: flex;
	font-family: 'Inter', Arial, Helvetica, sans-serif;
	width: 454px;
	height: 280px;
	padding: 0px;
	box-shadow: 2px 2px 15px -3px rgba(0, 0, 0, 0.1);

}

.card-movie img {
	width: 185px;
	height: 100%;
}

.card-movie__title {
	font-size: 20px;
	color: #000000;
	padding: 0;
	margin: 0;
}

.card-movie__date {
	font-size: 12px;
	display: inline-block;
	color: #827e7e;
	margin-bottom: 7px;
}

.card-movie__overview {
	color: #000;
  font-size: 12px;
  padding: 0;
  margin: 0;

}



.no-poster {
	display: block;
	font-size: 22px;
	font-weight: bold;
	color: #6b6b6b;
	width: 185px;
	padding: 10px 0 0 10px;
}
